
    <template>
      <section class="market-doc-container">
        <h2>Checkbox 复选框</h2>
<h3>基本使用</h3>
<p>可以使用<code>v-model</code>进行双向数据绑定。</p>
<mku-example>
        
        <template slot="source"><element-demo0 /></template>
        <template slot="highlight"><pre v-pre><code class="html">&lt;template&gt;
  &lt;div&gt;
    &lt;mku-checkbox v-model=&quot;value&quot;&gt;同意用户协议&lt;/mku-checkbox&gt;
    &lt;p&gt;当前checkbox的值：{{value}}&lt;/p&gt;
  &lt;/div&gt;
&lt;/template&gt;

&lt;script&gt;
export default {
  data () {
    return {
      value: false
    }
  }
}
&lt;/script&gt;
</code></pre></template></mku-example><h3>禁用</h3>
<mku-example>
        
        <template slot="source"><element-demo1 /></template>
        <template slot="highlight"><pre v-pre><code class="html">&lt;template&gt;
  &lt;div&gt;
    &lt;mku-checkbox v-model=&quot;value&quot; disabled&gt;禁用的复选框&lt;/mku-checkbox&gt;
    &lt;mku-checkbox v-model=&quot;value2&quot; disabled&gt;禁用的复选框&lt;/mku-checkbox&gt;
  &lt;/div&gt;
&lt;/template&gt;

&lt;script&gt;
export default {
  data () {
    return {
      value: false,
      value2: true
    }
  }
}
&lt;/script&gt;
</code></pre></template></mku-example><h3>组合使用</h3>
<mku-example>
        
        <template slot="source"><element-demo2 /></template>
        <template slot="highlight"><pre v-pre><code class="html">&lt;template&gt;
  &lt;div&gt;
  &lt;mku-checkbox
    v-model=&quot;checkAll&quot;
    :indeterminate=&quot;indeterminate&quot;
    @change=&quot;handleAllChange&quot;
  &gt;
    全部
  &lt;/mku-checkbox&gt;

  &lt;mku-checkbox-group v-model=&quot;value&quot; @change=&quot;handleGroupChange&quot;&gt;
    &lt;mku-checkbox label=&quot;a&quot;&gt;选项1&lt;/mku-checkbox&gt;
    &lt;mku-checkbox label=&quot;b&quot;&gt;选项2&lt;/mku-checkbox&gt;
    &lt;mku-checkbox label=&quot;c&quot;&gt;选项3&lt;/mku-checkbox&gt;
    &lt;mku-checkbox label=&quot;d&quot;&gt;选项4&lt;/mku-checkbox&gt;
  &lt;/mku-checkbox-group&gt;
  &lt;/div&gt;
&lt;/template&gt;

&lt;script&gt;
export default {
  data () {
    return {
      checkAll: false,
      indeterminate: true,
      value: ['a', 'b']
    }
  },
  methods: {
    handleAllChange (val) {
      this.indeterminate = false
      this.value = val ? ['a', 'b', 'c', 'd'] : []
      console.log(val)
    },
    handleGroupChange (val) {
      this.indeterminate = val.length &gt; 0 &amp;&amp; val.length &lt; 4
      this.checkAll = val.length === 4
    }
  }
}
&lt;/script&gt;
</code></pre></template></mku-example><h3>Checkbox Attributes</h3>
