<td style="text-align:left"><code>String|Boolean|Number</code></td>
<td style="text-align:left">-</td>
<td style="text-align:left">true</td>
<td style="text-align:left">否</td>
</tr>
<tr>
<td style="text-align:left">falseValue</td>
<td style="text-align:left">自定义<code>false</code>的值</td>
<td style="text-align:left"><code>String|Boolean|Number</code></td>
<td style="text-align:left">-</td>
<td style="text-align:left">false</td>
<td style="text-align:left">否</td>
</tr>
<tr>
<td style="text-align:left">disabled</td>
<td style="text-align:left">禁用复选框</td>
<td style="text-align:left"><code>Boolean</code></td>
<td style="text-align:left">-</td>
<td style="text-align:left"><code>false</code></td>
<td style="text-align:left">否</td>
</tr>
<tr>
<td style="text-align:left">indeterminate</td>
<td style="text-align:left">设置半选状态，仅单独使用时有效</td>
<td style="text-align:left"><code>Boolean</code></td>
<td style="text-align:left">-</td>
<td style="text-align:left"><code>false</code></td>
<td style="text-align:left">否</td>
</tr>
</tbody>
</table>
<h3>Checkbox Events</h3>
<table>
<thead>
<tr>
<th style="text-align:left">事件名</th>
